import { Container, Graphics } from "pixi.js"
import { Config } from "../../game/config"

export default class LoadingBar extends Container {
  // bar dimensions
  private readonly lHeight = 50
  private readonly lWidth = 300

  // screen dimensions
  private readonly _height
  private readonly _width

  constructor(width: number, height: number) {
    super()

    this._height = height
    this._width = width

    const outline = new Graphics()
    outline.lineStyle(5, Config.constants.white)

    outline.drawRect(
      this._width / 2 - this.lWidth / 2,
      this._height / 2 - this.lHeight / 2,
      this.lWidth,
      this.lHeight
    )

    this.addChild(outline)
  }

  public update(pct: number) {
    const fill = new Graphics()
    fill.lineStyle(5, Config.constants.white)
    fill.beginFill(Config.constants.white)

    fill.drawRect(
      this._width / 2 - this.lWidth / 2,
      this._height / 2 - this.lHeight / 2,
      (this.lWidth / 100) * pct,
      this.lHeight
    )

    if (this.children.length > 1) {
      // remove all childs except the first (the outline)
      this.removeChildren(1)
    }
    this.addChild(fill)
  }
}
