import { ObjectBase } from "./object-base"
import { Shell } from "../../game/objects/weapons/shell"
import { Muzzle } from "../../game/objects/weapons/muzzle"
import { IResourceDictionary, Sprite } from "pixi.js"

export class WeaponBase extends ObjectBase {
  protected _reloadTime: number
  protected _isReloading: boolean = false

  protected _power: number
  protected _maxRounds: number
  protected _rounds: number
  protected _sprite?: Sprite
  protected _roundsSprite?: Sprite
  private _shellSprites: Shell[] = []

  get rounds() {
    return this._rounds
  }

  get sprite() {
    return this._sprite
  }

  get roundsSprite() {
    return this._roundsSprite
  }

  get isReloading() {
    return this._isReloading
  }

  constructor(
    resources: IResourceDictionary,
    power: number,
    rounds: number
  ) {
    super(resources)

    this._power = power
    this._rounds = rounds
    this._maxRounds = rounds
    this._reloadTime = 5

    this.zIndex = 100

    this.x = -5
    this.y = 20
  }

  public fire(): boolean {
    if (this._rounds > 0 && !this._isReloading) {
      // muzzle flash
      const muzzleFlash = new Muzzle(this._resources, 2)

      muzzleFlash.x = 60
      muzzleFlash.y = -25
      this.addChild(muzzleFlash)

      this._rounds--

      const shell = new Shell(
        this._resources,
        this._roundsSprite?.texture!
      )

      const p = this.parent.parent
      shell.position = this.parent.position

      // add shell to the parent element because we want to let it fly around
      p.addChild(shell)

      this._shellSprites.push(shell)

      setTimeout(() => {
        p.removeChild(shell)
        this._shellSprites = this._shellSprites.splice(
          this._shellSprites.findIndex((s) => s === shell)
        )
      }, 3 * 1000)

      // todo: add fire sound

      return true
    }

    // todo: add empty clips sound

    return false
  }

  public tick(delta: number) {
    for (const shellSprite of this._shellSprites) {
      shellSprite.tick(delta)
    }
  }

  public reload(): boolean {
    if (this.isReloading) {
      return false
    }

    this._isReloading = true
    setTimeout(() => {
      this._rounds = this._maxRounds
      this._isReloading = false
    }, this._reloadTime * 1000)

    console.log("reload")
    return true
  }
}
