import { Config } from "../config"
import { ObjectBase } from "../../engine/objects/object-base"
import {
  IResourceDictionary,
  Sprite,
  Texture
} from "pixi.js"

export class Beacon extends ObjectBase {
  constructor(
    resources: IResourceDictionary,
    width: number,
    height: number
  ) {
    super(resources)

    this._isFacingRight = true

    this.height = height
    this.width = width

    const bg = new Sprite(
      Config.highlightBeacons
        ? Texture.WHITE
        : Texture.EMPTY
    )

    bg.width = width
    bg.height = height

    if (Config.highlightBeacons) {
      bg.tint = 0xff0000
    }

    this.addChild(bg)
  }
}
