import { Container, Text, TextStyle } from "pixi.js"

export default class FpsCounter extends Container {
  private _fpsCount: number = 0
  private _fpsTimer: number = 0
  private readonly _fps?: Text

  constructor() {
    super()

    this._fps = new Text("FPS: ", {
      fontWeight: "16px",
      fill: "#fff"
    } as TextStyle)

    this._fps.x = 10
    this._fps.y = 10

    this.addChild(this._fps)
  }

  tick(delta: number) {
    if (this._fps) {
      this._fpsCount++
      this._fpsTimer += delta

      // reset after 1 second
      if (this._fpsTimer > 1000) {
        // update text
        this._fps.text = "FPS: " + this._fpsCount

        this._fpsCount = 0
        this._fpsTimer = 0
      }
    }
  }
}
