import { Config } from "../../game/config"
import {
  Text,
  Container,
  Graphics,
  IResourceDictionary,
  TextStyle
} from "pixi.js"
import { MapBase } from "../maps/map-base";

/**
 *
 */
export class ObjectBase extends Container {
  protected _outlineContainer?: Container
  protected _map?: MapBase

  protected _baseScale: number = 1

  /**
   *
   * @type {Object}
   * @protected
   */
  protected _resources: IResourceDictionary

  /**
   * Is the this one facing right?
   * @type {boolean}
   * @protected
   */
  protected _isFacingRight: boolean = true

  /**
   * the object velocity x
   * @type {number}
   * @protected
   */
  protected _vx = 0

  /**
   * the object velocity y
   * @type {number}
   * @protected
   */
  protected _vy = 0

  /**
   * the objects rotation velocity
   * @type {number}
   * @private
   */
  protected _vr = 0

  /**
   *
   * @param resources
   */
  constructor(resources: IResourceDictionary) {
    super()

    this._resources = resources
  }

  /**
   *
   * @returns {number}
   */
  get vx() {
    return this._vx
  }

  /**
   *
   * @returns {number}
   */
  get vy() {
    return this._vy
  }

  /**
   *
   * @returns {number}
   */
  get vr() {
    return this._vr
  }

  /**
   *
   * @returns {Number}
   */
  get realx() {
    return this.x - Math.abs(this.width) / 2
  }

  set map(map: MapBase) {
    this._map = map
  }

  public tick(delta: number) {
    if (this.vx !== 0 || this.vy !== 0 || this.vr !== 0) {
      this.x += this.vx
      this.y += this.vy
      this.rotation += this.vr
    }
  }

  /**
   *
   * @param actor
   * @public
   */
  public collides(actor: ObjectBase) {
    const actorBounds = actor.getBounds()
    const thisBounds = this.getBounds()

    const collides =
      actorBounds.x + actorBounds.width > thisBounds.x &&
      actorBounds.x < thisBounds.x + thisBounds.width &&
      actorBounds.y + actorBounds.height > thisBounds.y &&
      actorBounds.y < thisBounds.y + thisBounds.height

    if (collides && Config.logCollisions) {
      console.log("this", thisBounds)
      console.log("actor", actorBounds)
    }

    return collides
  }

  public adaptScale(y: number, height: number) {
    const scaleOffset = height - y
    let scale = this._baseScale - scaleOffset / 1000

    this.scale.x = scale
    this.scale.y = scale

    this._adaptScaleBasedOnOrientation()
  }

  /**
   *
   * @private
   */
  protected _adaptScaleBasedOnOrientation() {
    let scale = this.scale.x
    if (!this._isFacingRight && scale > 0) {
      this.scale.x = -scale
    }

    if (this._isFacingRight && scale < 0) {
      this.scale.x = Math.abs(scale)
    }
  }

  public outline() {
    // do we have a outline?
    if (this._outlineContainer) {
      // yep remove if from child
      this.parent
        ? this.parent.removeChild(this._outlineContainer)
        : this.removeChild(this._outlineContainer)
      // delete it
      delete this._outlineContainer
    }

    this._adaptScaleBasedOnOrientation()

    this._outlineContainer = new Container()
    const b = this.getBounds()
    const lb = this.parent ? this.parent.toLocal(b) : b

    if (Config.showObjectCoordinates) {
      const coordinates = new Text(
        `x: ${b.x.toFixed(0)} y: ${b.y.toFixed(
          0
        )}\nw: ${b.width.toFixed(0)} h: ${b.height.toFixed(
          0
        )}`,
        {
          fontSize: "10px",
          fill: "#fff"
        } as TextStyle
      )

      coordinates.x = lb.x
      coordinates.y = lb.y

      this._outlineContainer.addChild(coordinates)
    }

    // create new
    const outline = new Graphics()
    outline.lineStyle(1, 0x000)

    outline.drawRect(lb.x, lb.y, b.width, b.height)

    outline.endFill()

    // attach to object
    this._outlineContainer.addChild(outline)

    this.parent
      ? this.parent.addChild(this._outlineContainer)
      : this.addChild(this._outlineContainer)
  }
}
