import { AnimationBase } from "./animation-base"
import { TextWithBackground } from "./text-with-background"
import { IResourceDictionary } from "pixi.js"
import { Config } from "../../game/config"

/**
 *
 */
export class TextBubble extends AnimationBase {
  constructor(
    resources: IResourceDictionary,
    text: string,
    duration: number,
    done: () => void
  ) {
    super(resources)

    this.addChild(
      new TextWithBackground(
        resources,
        text,
        "8px",
        "#fff",
        Config.constants.black
      )
    )

    setTimeout(() => {
      done()
    }, duration * 1000)
  }
}
