import { IDictionary } from "./tools/IDictionary"

type d<T> = IDictionary<IDictionary<T>>

interface Event {
  pressEvent?: () => void
  releaseEvent?: () => void
}

type Events = d<Event>

interface State {
  isPressed: boolean
}

type States = d<State>

export class Gamepads {
  static _gamepads: IDictionary<Gamepad> = {}
  static _state: States = {}
  static _events: Events = {}

  static init(events: Events) {
    Gamepads._events = events

    Gamepads.scanGamepads()

    return Gamepads._gamepads[0] || null
  }

  static connectHandler(event: GamepadEvent) {
    console.log("gamepad connected")

    Gamepads.addGamepad(event.gamepad)
  }

  static addGamepad(gamepad: Gamepad) {
    console.log("adding gamepad", gamepad)

    Gamepads._gamepads[gamepad.index] = gamepad
    Gamepads._state[gamepad.index] = {}
  }

  static disconnectHandler(event: GamepadEvent) {
    console.log("gamepad disconnected")

    Gamepads.removeGamepad(event.gamepad)
  }

  static removeGamepad(gamepad: Gamepad) {
    console.log("removing gamepad")
    delete Gamepads._gamepads[gamepad.index]
    delete Gamepads._state[gamepad.index]
  }

  static updateStatus() {
    Gamepads.scanGamepads()
    for (const gamepadIndex in Gamepads._gamepads) {
      if (Gamepads._gamepads.hasOwnProperty(gamepadIndex)) {
        const gamepad: Gamepad =
          Gamepads._gamepads[gamepadIndex]

        if (gamepad.axes) {
          //console.log(gamepad.axes)
        }

        // sometimes controllers dont have buttons
        if (gamepad.buttons) {
          for (const buttonIndex in gamepad.buttons) {
            if (
              gamepad.buttons.hasOwnProperty(buttonIndex)
            ) {
              const button: GamepadButton =
                gamepad.buttons[buttonIndex]

              Gamepads._state[gamepadIndex][buttonIndex] =
                Gamepads._state[gamepadIndex][
                  buttonIndex
                ] || {}
              Gamepads._events[gamepadIndex][buttonIndex] =
                Gamepads._events[gamepadIndex][
                  buttonIndex
                ] || {}

              const state: State =
                Gamepads._state[gamepadIndex][buttonIndex]
              const event: Event =
                Gamepads._events[gamepadIndex][buttonIndex]

              if (button.pressed && !state.isPressed) {
                console.log(
                  "button pressed",
                  gamepadIndex,
                  buttonIndex
                )

                state.isPressed = true

                if (event.pressEvent) {
                  event.pressEvent()
                }
              }

              if (!button.pressed && state.isPressed) {
                console.log(
                  "button released",
                  gamepadIndex,
                  buttonIndex
                )

                state.isPressed = false

                if (event.releaseEvent) {
                  event.releaseEvent()
                }
              }
            }
          }
        }
      }
    }
  }

  static scanGamepads() {
    const gamepads: Gamepad[] = navigator.getGamepads
      ? navigator.getGamepads()
      : // @ts-ignore
      navigator.webkitGetGamepads
      ? // @ts-ignore
        navigator.webkitGetGamepads()
      : []

    for (const gamepad of gamepads) {
      // sometimes gamepad is undefined?!
      if (gamepad) {
        const gamepadIndex = gamepad.index

        // do we have this already in records?
        if (gamepadIndex in Gamepads._gamepads) {
          Gamepads._gamepads[gamepadIndex] = gamepad
        } else {
          console.log(
            "found new gamepad during scan, adding",
            gamepadIndex
          )

          Gamepads.addGamepad(gamepad)
        }
      }
    }
  }
}

// register events
// @ts-ignore
window.addEventListener(
  "gamepadconnected",
  Gamepads.connectHandler
)
// @ts-ignore
window.addEventListener(
  "gamepaddisconnected",
  Gamepads.disconnectHandler
)
