import {
  Application,
  Container,
  ILoaderOptions,
  IResourceDictionary,
  Loader
} from "pixi.js"
import "../engine/tools/pixi"
import Game from "./game"
import { Config } from "./config"
import { assets } from "./assets"
import FpsCounter from "../engine/objects/fsp-counter"
import LoadingBar from "../engine/objects/loading-bar"

const width = Config.width
const height = Config.height

export default class Maension {
  private _lastFrame: number = 0
  private _game?: Game

  private readonly _fpsCounter?
  private readonly _container: HTMLDivElement
  private readonly _app: Application
  private readonly _root: Container

  private readonly _loadingBar: LoadingBar

  constructor(container: HTMLDivElement) {
    this._container = container

    this._app = new Application({
      width,
      height,
      backgroundColor: Config.constants.black
    })

    this._container.appendChild(this._app.view)

    console.log(
      "Viewport",
      this._container.clientWidth,
      this._container.clientHeight
    )

    // create the root of the scene graph
    // @ts-ignore
    this._root = new Container()

    // @ts-ignore
    this._app.stage.addChild(this._root)

    this.resize()

    if (Config.showFPS) {
      this._fpsCounter = new FpsCounter()

      this._app.stage.addChild(this._fpsCounter)
    }

    this._loadingBar = new LoadingBar(width, height)
    this._app.stage.addChild(this._loadingBar)

    window.addEventListener(
      "resize",
      this.resize.bind(this)
    )

    // start ticking
    this._tick(0)
  }

  resize() {
    const vpw = window.innerWidth - 50 // Width of the viewport
    const vph = window.innerHeight // Height of the viewport
    let nvw // New game width
    let nvh // New game height

    // The aspect ratio is the ratio of the screen's sizes in different dimensions.
    // The height-to-width aspect ratio of the game is HEIGHT / WIDTH.

    if (vph / vpw < height / width) {
      // If height-to-width ratio of the viewport is less than the height-to-width ratio
      // of the game, then the height will be equal to the height of the viewport, and
      // the width will be scaled.
      nvh = vph
      nvw = (nvh * width) / height
    } else {
      // In the else case, the opposite is happening.
      nvw = vpw
      nvh = (nvw * height) / width
    }

    if (nvw > width) {
      nvw = width
    }

    if (nvh > height) {
      nvh = height
    }

    // Set the game screen size to the new values.
    // This command only makes the screen bigger --- it does not scale the contents of the game.
    // There will be a lot of extra room --- or missing room --- if we don't scale the stage.
    this._app.renderer.resize(nvw, nvh)

    // This command scales the stage to fit the new size of the game.
    this._root.scale.set(nvw / width, nvh / height)
  }

  /**
   *
   */
  public run() {
    const options = {
      crossOrigin: true
    } as ILoaderOptions

    const assetPath = "assets"

    const loader = new Loader()

    Object.keys(assets).forEach((id) => {
      loader.add(id, `${assetPath}/${assets[id]}`, options)
    })

    loader.onComplete.once(this._assetsLoaded.bind(this))

    // resources loaded
    loader.load()

    loader.onProgress.add((event: any) => {
      this._loadingBar.update(event.progress)
    })
  }

  /**
   *
   * @param loader
   * @param resources
   * @private
   */
  private _assetsLoaded(
    loader: Loader,
    resources: IResourceDictionary
  ) {
    this._app.stage.removeChild(this._loadingBar)

    // loading done, start game
    this._game = new Game(
      resources,
      this._root,
      width,
      height
    )

    console.log("Mänsion:", "Init done, ready to scare!")
  }

  /**
   *
   * @private
   */
  private _tick(elapsed: number) {
    // calculate frame delta
    let delta = elapsed - this._lastFrame
    this._lastFrame = elapsed

    if (this._fpsCounter) {
      this._fpsCounter.tick(delta)
    }

    // update game
    if (this._game) {
      this._game.tick(elapsed, delta)
    }

    // render the container
    this._app.render()

    // loop
    requestAnimationFrame(this._tick.bind(this))
  }
}
