import { AnimationBase } from "./animation-base"
import {
  Text,
  IResourceDictionary,
  SCALE_MODES,
  Sprite,
  TextStyle,
  Texture,
  settings
} from "pixi.js"
import { Config } from "../../game/config"

settings.SCALE_MODE = SCALE_MODES.NEAREST

/**
 *
 */
export class TextWithBackground extends AnimationBase {
  constructor(
    resources: IResourceDictionary,
    text: string,
    fontSize: string = "8px",
    fontColor: string = "#fff",
    background?: number,
    dropShadowColor?: string
  ) {
    super(resources)

    const style: TextStyle = {
      fontSize,
      fill: fontColor,
      fontFamily: Config.fontFamily
    } as TextStyle

    if (dropShadowColor) {
      style.dropShadow = true
      style.dropShadowColor = dropShadowColor
    }

    const t = new Text(text, style)

    if (!isNaN(background!)) {
      const bg = new Sprite(Texture.WHITE)

      bg.width = t.width
      bg.height = t.height
      bg.tint = background!

      this.addChild(bg)
    }

    this.addChild(t)
  }
}
