import {
  AnimatedSprite,
  IResourceDictionary,
  Texture
} from "pixi.js"

/**
 * @class
 */
export class AnimationHelper {
  static loadClip(
    resources: IResourceDictionary,
    which: string,
    speed: number,
    scale: number
  ): AnimatedSprite {
    const frames: Texture[] = []

    if (!resources[which]) {
      throw new Error(`resources for ${which} not found!`)
    }

    for (const textureId in resources[which].textures) {
      let texture = resources[which]!.textures![textureId]
      frames.push(texture)
    }

    const clip = new AnimatedSprite(frames)

    clip.animationSpeed = speed
    clip.scale.x = scale
    clip.scale.y = scale
    clip.anchor.x = 0.5

    return clip
  }
}
