/**
 * @class
 */
export class Keyboard {
  /**
   *
   * @type {boolean}
   * @private
   */
  private _isDown = false
  /**
   *
   * @type {boolean}
   * @private
   */
  private _isUp = true

  // events
  /**
   *
   * @type {KeyboardEvent}
   */
  public pressEvent: (() => void) | null = null
  /**
   *
   * @type {KeyboardEvent}
   */
  public releaseEvent: (() => void) | null = null

  constructor(
    private readonly _key: string,
    private readonly _ignoreKeyup: boolean
  ) {
    //Attach event listeners
    window.addEventListener(
      "keydown",
      this._downHandler.bind(this),
      false
    )
    window.addEventListener(
      "keyup",
      this._upHandler.bind(this),
      false
    )
  }

  get isDown() {
    return this._isDown
  }

  /**
   *
   * @param event
   * @private
   */
  private _downHandler(event: KeyboardEvent) {
    if (event.key === this._key) {
      event.preventDefault()

      // if the key is up and we have a press handler
      if (this._isUp && this.pressEvent) {
        this.pressEvent()
      }

      this._isDown = true
      this._isUp = false
    }
  }

  /**
   *
   * @param event
   * @private
   */
  private _upHandler(event: KeyboardEvent) {
    if (event.key === this._key) {
      event.preventDefault()

      // if the key is down and we have a press handler
      if (this._isDown && this.releaseEvent) {
        this.releaseEvent()
      }

      this._isDown = false
      this._isUp = true
    }
  }
}
