import { MathHelper } from "../../engine/tools"
import { tween, easing, chain } from "popmotion"

import { Container, Sprite, Texture } from "pixi.js"

/**
 *
 */
export class Lightning extends Container {
  constructor() {
    super()

    this.pivot.x = this.width / 2
    this.pivot.y = this.height / 2

    const sprite = new Sprite(Texture.WHITE)

    sprite.width = 1000
    sprite.height = 1000
    this.alpha = 0

    setInterval(() => {
      this.shine()
    }, MathHelper.getRandomInt(5, 10) * 1000)

    setInterval(() => {
      this.shine()
    }, MathHelper.getRandomInt(10, 20) * 1000)

    this.addChild(sprite)
  }

  private shine() {
    const max = 0.75
    const min = 0.0

    chain(
      tween({
        from: min,
        to: max,
        duration: 75,
        ease: [easing.createExpoIn(3)]
      }),
      tween({
        from: max,
        to: min,
        duration: 400,
        ease: [easing.easeOut]
      })
    ).start((alpha: number) => (this.alpha = alpha))
  }
}
