import {
  Beretta,
  Shotgun,
  WeaponBase
} from "../objects/weapons"
import {
  ActorBase,
  ActorStates
} from "../../engine/actors/actor-base"
import {
  AnimationHelper,
  MathHelper
} from "../../engine/tools"
import { IResourceDictionary, Point } from "pixi.js"
import { Flashlight } from "../objects/weapons/flashlight"

const weapons: {
  shotgun?: Shotgun
  beretta?: Beretta
} = {}

/**
 *
 */
export class Player extends ActorBase {
  private _weapon: WeaponBase
  private _name: string = "Taro"

  flashlight?: Flashlight

  get weapon(): WeaponBase {
    return this._weapon
  }

  get playerName() {
    return this._name
  }

  /**
   *
   * @param resources
   * @param skin
   */
  constructor(
    resources: IResourceDictionary,
    skin: string
  ) {
    super(resources, skin, 3)

    this._baseScale = 2

    this._health = 77

    // load the clips for this object
    this._clips[
      this._skin + ".idle"
    ] = AnimationHelper.loadClip(
      this._resources,
      this._skin + ".idle",
      0.1,
      1
    )
    this._clips[
      this._skin + ".idle_handgun"
    ] = AnimationHelper.loadClip(
      this._resources,
      this._skin + ".idle_handgun",
      0.1,
      1
    )
    /*    this._clips[this._skin + ".walk"] =
          AnimationHelper.loadClip(
            this._resources, this._skin + ".walk", 0.2, 1
          );
        this._clips[this._skin + ".punch"] =
          AnimationHelper.loadClip(
            this._resources, this._skin + ".punch", 0.3, 1
          );
        this._clips[this._skin + ".kick"] =
          AnimationHelper.loadClip(
            this._resources, this._skin + ".kick", 0.2, 1
          );
        this._clips[this._skin + ".gothit"] =
          AnimationHelper.loadClip(
            this._resources, this._skin + ".gothit", 0.2, 1
          );
        this._clips[this._skin + ".died"] =
          AnimationHelper.loadClip(
            this._resources, this._skin + ".died", 0.2, 1
          );*/

    weapons.shotgun = new Shotgun(resources)
    weapons.beretta = new Beretta(resources)
    this.flashlight = new Flashlight(resources)

    // todo get this from equipment
    this._weapon = weapons.beretta
    this.addChild(this._weapon)

    // go to idle
    this.relax()
  }

  private relax() {
    //  this._idle(this._weapon && this._weapon instanceof Beretta ? ".idle_handgun" : ".idle")
    this._idle()
  }

  public reload() {
    // todo check equipment for shells
    this._weapon.reload()
  }

  public switchWeapon() {
    if (!this.weapon.isReloading) {
      this.removeChild(this._weapon)

      if (this._weapon instanceof Shotgun) {
        this._weapon = weapons.beretta!
      } else if (this._weapon instanceof Beretta) {
        this._weapon = weapons.shotgun!
      }

      this.toggleFlashlight()

      this.addChild(this._weapon)
    }
  }

  private toggleFlashlight() {
    if (this._map && this.flashlight) {
      if (
        this._map.foregroundContainer.children.find(
          (c) => c === this.flashlight
        )
      ) {
        this._map.foregroundContainer.removeChild(
          this.flashlight
        )
      } else {
        this.flashlight.position.set(this.x, this.y)
        this._map.foregroundContainer.addChild(
          this.flashlight
        )

        this._map.foregroundContainer.children.sort(
          (a, b) => a.zIndex - b.zIndex
        )
      }
    }
  }

  public updateCrosshairPosition(crosshairPosition: Point) {
    const angle: number = this._isFacingRight
      ? MathHelper.angleBetweenRad(
          this.weaponLocation,
          crosshairPosition
        )
      : -MathHelper.angleBetweenRad(
          crosshairPosition,
          this.weaponLocation
        )
    this._weapon.rotation = angle
  }

  get weaponLocation(): Point {
    return this.toGlobal(this._weapon.position)
  }

  /**
   *
   * @returns {boolean}
   * @private
   */
  private _canBeControlled() {
    if (this._state === ActorStates.died) {
      console.log("Cant be controlled, player is dead")
      return false
    }

    return (
      this._state === ActorStates.idle ||
      this._state === ActorStates.walking
    )
  }

  /**
   *
   * @private
   */
  protected _died() {
    super._died()

    // todo: add dying animation
    // the died animation is way lower so we need to
    // adjust the hight a bit
    this.y += this.height / 2

    this.height = this.height / 2
  }

  /**
   * @public
   */
  public fire(): boolean {
    if (!this._canBeControlled()) {
      return false
    }

    // try to fires
    const fire = this._weapon.fire()
    if (fire) {
      // we fired, announce
      this.speak("Päng!")

      // set state, remove velo
      this._setState(ActorStates.fire)
      this._vx = 0
      this._vy = 0

      // play fire animation
      this._playClip(this._skin + ".punch", false).then(
        () => {
          // once done, go back to idle
          this.relax()
        }
      )
      return true
    } else {
      this.relax()
      return false
    }
  }

  /**
   * @public
   */
  public moveRight() {
    if (this._canBeControlled()) {
      this._vx = this._speed
      this._playClip(this._skin + ".walk", true)
      this._setState(ActorStates.walking)
    }
  }

  /**
   * @public
   */
  public moveLeft() {
    if (this._canBeControlled()) {
      this._vx = -this._speed
      this._playClip(this._skin + ".walk", true)
      this._setState(ActorStates.walking)
      this._adaptScaleBasedOnOrientation()
    }
  }

  /**
   * @public
   */
  public stopMoveHorizontally() {
    this._vx = 0
    if (this._vy === 0 && this._canBeControlled()) {
      this.relax()
    }
  }

  /**
   * @public
   */
  public moveUp() {
    if (this._canBeControlled()) {
      this._vy = -this._speed
      this._playClip(this._skin + ".walk", true)
      this._setState(ActorStates.walking)
    }
  }

  /**
   * @public
   */
  public moveDown() {
    if (this._canBeControlled()) {
      this._vy = this._speed
      this._playClip(this._skin + ".walk", true)
      this._setState(ActorStates.walking)
    }
  }

  /**
   * @public
   */
  public stopMoveVertically() {
    this._vy = 0
    if (this._vx === 0 && this._canBeControlled()) {
      this.relax()
    }
  }

  public speak(text: string) {
    super.speak(`${this._name}: ${text}`)
  }

  public move(
    maxHeight: number,
    maxWidth: number
  ): boolean {
    let playerMoved = false

    if (
      // do we have velocity?
      this.vy !== 0 &&
      this.y + this.vy > 0 &&
      // dont move lower than the bottom
      this.y + this.height + this.vy < maxHeight
    ) {
      this.y += this.vy
      playerMoved = true
    }

    // move right
    if (this.vx > 0) {
      // dont walk further than the right border of the map
      if (
        this.realx + Math.abs(this.width) + 7 + this.vx <
        maxWidth
      ) {
        this.x += this.vx
        playerMoved = true
      }
    }
    // move left
    else if (this.vx < 0) {
      // detect left col
      if (this.realx + this.vx >= 0) {
        this.x += this.vx
        playerMoved = true
      }
    }

    if (playerMoved) {
      this.adaptScale(this.y, maxHeight)
      this.flashlight?.position.set(this.x, this.y)
      this.moved()
    }

    return playerMoved
  }

  public tick(delta: number) {
    this.weapon.tick(delta)
  }
}
