import { MapBase } from "../../engine/maps/map-base"
import {
  IResourceDictionary,
  Sprite,
  TilingSprite
} from "pixi.js"

/**
 *
 */
export class Street extends MapBase {
  constructor(
    resources: IResourceDictionary,
    width: number
  ) {
    super(resources, width, "maps.street")

    this._setup()
  }

  /**
   *
   * @private
   */
  protected _setup() {
    // background

    // load city first because we need the width
    // @ts-ignore
    let city = new Sprite(this._textures["city01.png"])
    let cityWidth = city.width
    city.scale.x = 2
    city.scale.y = 2.1
    city.y = -100

    let concret = new TilingSprite(
      // @ts-ignore
      this._textures["concret.png"]
    )
    concret.width = cityWidth
    concret.height = 300
    concret.scale.x = 2
    concret.scale.y = 1.44
    concret.y = 330

    // concret should be in the back so add it first
    this._backgroundContainer.addChild(concret)

    this._backgroundContainer.addChild(city)

    // foreground
    // @ts-ignore
    let police = new Sprite(this._textures["police.png"])
    police.scale.x = 2
    police.scale.y = 2

    police.y = this.height - police.height * 2
    police.x = 300
    this._foregroundContainer.addChild(police)
  }
}
