import { WeaponBase } from "../../../engine/objects/weapon-base"
import { IResourceDictionary, Sprite } from "pixi.js"

/**
 *
 */
export class Shotgun extends WeaponBase {
  /**
   *
   * @param resources
   */
  constructor(resources: IResourceDictionary) {
    super(resources, 55, 6)

    const which = "weapons.shotgun"

    const res = resources![which]

    this._roundsSprite = new Sprite(
      res.textures!["shell.png"]!
    )

    this._sprite = new Sprite(res.textures!["shotgun.png"]!)

    this._sprite.scale.x = 0.1
    this._sprite.scale.y = 0.1

    // shotgun should be in the back so add it first
    this.addChild(this._sprite)
  }
}
