import { MapBase } from "../../engine/maps/map-base"
import { IResourceDictionary, Sprite } from "pixi.js"

/**
 *
 */
export class HouseOutside extends MapBase {
  constructor(
    resources: IResourceDictionary,
    width: number
  ) {
    super(resources, width, "maps.house")

    this._setup()
  }

  /**
   *
   * @private
   */
  protected _setup() {
    let city = new Sprite(
      // @ts-ignore
      this._textures["house.png"]
    )

    city.scale.x = 1.3
    city.scale.y = 1.3

    this._backgroundContainer.addChild(city)

    setInterval(() => {
      //z1.play();
    }, 30 * 1000)

    setInterval(() => {
      //z2.play();
    }, 67 * 1000)
  }
}
