import { IDictionary } from "../tools/IDictionary"
import { ObjectBase } from "./object-base"
import { AnimatedSprite } from "pixi.js"

/**
 *
 */
export class AnimationBase extends ObjectBase {
  /**
   * array for animations
   * @type {{}}
   * @protected
   */
  protected _clips: IDictionary<AnimatedSprite> = {}

  /**
   * current clip that is beeing played
   * @type {null}
   * @private
   */
  protected _currentClip?: AnimatedSprite

  /**
   *
   * @param isFacingRight
   * @protected
   */
  protected _setIsFacingRight(isFacingRight: boolean) {
    this._isFacingRight = isFacingRight
    this._adaptScaleBasedOnOrientation()
  }

  protected _playClip(
    which: string,
    loop: boolean,
    dontRemove?: boolean
  ) {
    if (this._currentClip) {
      this.removeChild(this._currentClip)
      this._currentClip = undefined
    }

    return new Promise(
      (resolve: Function, reject: Function) => {
        // load punch clip
        const clipToPlay = this._clips[which]

        if (!clipToPlay) {
          const err =
            "AnimationBase: failed to load clip: " + which
          console.log(err, this._clips)
          reject(err)
        }

        clipToPlay.zIndex = 1

        // play clip
        this.addChild(clipToPlay)

        // set the current playing clip
        this._currentClip = clipToPlay

        this._adaptScaleBasedOnOrientation()

        this._currentClip.loop = loop
        this._currentClip.gotoAndPlay(0)

        this.children.sort((a, b) => a.zIndex - b.zIndex)

        if (!loop) {
          clipToPlay.onComplete = () => {
            clipToPlay.stop()

            if (!dontRemove) {
              this.removeChild(clipToPlay)
            }

            resolve()
          }
        }
      }
    )
  }
}
