import { Config } from "./config"
import { Beacon, Blood, Crosshair, Impact } from "./objects"
import {
  ActorBase,
  ActorStates,
  EnemyBase,
  Player
} from "./actors"
import {
  HouseOutside,
  Loading,
  MaensionOutside,
  Street,
  Title
} from "./maps"
import {
  Gamepads,
  Keyboard,
  MathHelper,
  IDictionary
} from "../engine/tools"
import { HUD } from "./ui"
import {
  Container,
  DisplayObject,
  Graphics,
  InteractionEvent,
  IResourceDictionary,
  Point
} from "pixi.js"
import GameBase from "../engine/game-base"
import { MapBase } from "../engine/maps/map-base"

let maps: IDictionary<MapBase> = {}

/**
 * @class
 */
export default class Game extends GameBase {
  private _hud?: HUD
  private _player?: Player

  // special items that are nor visible
  private _beacon?: Beacon
  private _enemies: EnemyBase[] = []
  private _mouseLocation?: Point

  private _mouseLine?: Graphics
  private _crosshair?: Crosshair

  /**
   *
   * @param resources
   * @param stage
   * @param width
   * @param height
   */
  constructor(
    resources: IResourceDictionary,
    stage: Container,
    width: number,
    height: number
  ) {
    super(resources, stage, width, height)

    maps.street = new Street(this._resources, this._width)

    maps.house = new HouseOutside(
      this._resources,
      this._width
    )

    maps.maension = new MaensionOutside(
      this._resources,
      this._width
    )

    maps.loading = new Loading(this._resources, this._width)

    maps.title = new Title(
      this._resources,
      this._width,
      this._height
    )

    this._init()
  }

  private switchMap(newMap: MapBase): MapBase {
    this._stage.removeChild(this._map!)
    this._stage.removeChild(this._hud!)

    this._map = newMap!
    this._stage.addChild(this._map)

    if (this._map.withPlayer) {
      this._player!.x = 60
      this._player!.y = 400

      this._player!.adaptScale(
        this._player!.y,
        this._height
      )
      this._player!.map = this._map

      // add player to stage
      this._map.actorContainer.addChild(this._player!)
    }

    if (this._map.withCrosshair) {
      this._crosshair = new Crosshair(this._resources)
      this._crosshair.adaptScale(
        this._player!.y,
        this._height
      )

      this._stage.cursor = "none"
    }

    if (this._map.withHud) {
      this._stage.addChild(this._hud!)
      this._updateHud()
    }

    if (this._map.withMouse) {
      this._attachMouse()
      this._updateMouseLocation(
        new Point(this._width / 2, this._height / 2)
      )
    }

    return newMap!
  }

  /**
   *
   * @private
   */
  private _init() {
    // create player
    this._player = new Player(this._resources, "taro")

    this._hud = new HUD(
      this._resources,
      this._player,
      this._width,
      this._height
    )

    const map = this.switchMap(maps.title)

    // todo change this
    map.onDone = () => {
      this.switchMap(maps.maension!)
    }

    // for testing
    // todo remove
    this._beacon = new Beacon(this._resources, 80, 5)

    // attach the keyboard to player
    const gamepad = this._attachGamepad()

    // no gamepad, go to stinky keyboard
    if (!gamepad) {
      console.log("no gamepad, attaching stinky keyboard")
      this._attachKeyboard()
    }

    // init enemies
    // todo let map handle this
    this._enemies = []

    // this game is running baby
    this._isRunning = true

    console.log("Game:", "Game init done!")
  }

  private _attachMouse() {
    this._mouseLine = new Graphics()
    this._stage.addChild(this._mouseLine!)
    if (this._crosshair) {
      this._stage.addChild(this._crosshair)
    }

    this._stage.interactive = true
    this._stage.on("mousemove", (e: InteractionEvent) => {
      this._updateMouseLocation(e.data.global)
    })

    this._stage.on("mousedown", () => this._fire())

    this._stage.on(
      "mouseout",
      () => (this._isRunning = false)
    )

    this._stage.on(
      "mouseover",
      () =>
        (this._isRunning =
          this._player!.state !== ActorStates.died)
    )
  }

  private _updateCrosshair() {
    if (
      this._player &&
      this._player.state !== ActorStates.died &&
      this._crosshair &&
      this._mouseLocation &&
      this._isRunning
    ) {
      this._crosshair.x =
        this._mouseLocation.x - this._crosshair.width / 2
      this._crosshair.y =
        this._mouseLocation.y - this._crosshair.height / 2

      // flip player based on crosshair position
      if (
        this._crosshair!.x <
        this._player!.toGlobal(this._stage).x
      ) {
        this._player.flip(false)
      } else {
        this._player.flip(true)
      }

      // update player based on crosshair
      this._player.updateCrosshairPosition(
        this._stage.toGlobal(this._crosshair?.position!)
      )

      // adapt crosshair scale based on player location
      this._crosshair.adaptScale(
        this._player!.y,
        this._height
      )
    }
  }

  private _fire() {
    if (this._isRunning) {
      if (this._player?.fire()) {
        // hit location
        const impact = new Impact(this._resources)
        impact.scale.x = 0.1
        impact.scale.y = 0.1

        const impactLocation = this._map!.toLocal(
          this._crosshair!
        )
        impact.x =
          impactLocation.x +
          this._crosshair!.width / 2 -
          impact.width / 2
        impact.y =
          impactLocation.y +
          this._crosshair!.height / 2 -
          impact.height / 2

        this._map?.backgroundContainer.addChild(impact)

        this._updateHud()
      }
    }
  }

  private _moveRight() {
    if (this._isRunning && this._player) {
      this._player.moveRight()
    }
  }

  private _moveLeft() {
    if (this._isRunning && this._player) {
      this._player.moveLeft()
    }
  }

  private _moveUp() {
    if (this._isRunning && this._player) {
      this._player.moveUp()
    }
  }

  private _moveDown() {
    if (this._isRunning && this._player) {
      this._player.moveDown()
    }
  }

  private _stopMoveVertically() {
    if (this._isRunning) {
      this._player?.stopMoveVertically()
    }
  }

  private _stopMoveHorizontally() {
    if (this._isRunning) {
      this._player?.stopMoveHorizontally()
    }
  }

  private _attachKeyboard() {
    const left = new Keyboard("ArrowLeft", true),
      up = new Keyboard("ArrowUp", true),
      right = new Keyboard("ArrowRight", true),
      down = new Keyboard("ArrowDown", true),
      w = new Keyboard("w", false),
      r = new Keyboard("r", false),
      enter = new Keyboard("Enter", false)

    w.pressEvent = () => this._switchWeapon()
    r.pressEvent = () => this._reload()

    right.pressEvent = () => this._moveRight()
    right.releaseEvent = () => {
      if (!left.isDown) {
        this._stopMoveHorizontally()
      }
    }

    left.pressEvent = () => this._moveLeft()
    left.releaseEvent = () => {
      if (!right.isDown) {
        this._stopMoveHorizontally()
      }
    }

    up.pressEvent = () => this._moveUp()
    up.releaseEvent = () => {
      if (!down.isDown) {
        this._stopMoveVertically()
      }
    }

    down.pressEvent = () => this._moveDown()
    down.releaseEvent = () => {
      if (!up.isDown) {
        this._stopMoveVertically()
      }
    }

    enter.pressEvent = () => {
      if (!this._isRunning) {
        this._init()
      }
    }
  }

  private _attachGamepad(): boolean {
    return !!Gamepads.init({
      // first gamepad
      0: {
        // button 1 pressed
        1: {
          pressEvent: () => {
            this._fire()
          }
        },
        // button 15 pressed
        15: {
          pressEvent: () => this._moveRight(),
          releaseEvent: () => this._stopMoveHorizontally()
        },
        // button 14 pressed
        14: {
          pressEvent: () => this._moveLeft(),
          releaseEvent: () => this._stopMoveHorizontally()
        },
        // button 12 pressed
        12: {
          pressEvent: () => this._moveUp(),
          releaseEvent: () => this._stopMoveVertically()
        },
        // button 13 pressed
        13: {
          pressEvent: () => this._moveDown(),
          releaseEvent: () => this._stopMoveVertically()
        },
        // button 9 pressed
        9: {
          pressEvent: () => {
            if (!this._isRunning) {
              this._init()
            }
          }
        }
      }
    })
  }

  /**
   * this will end the game
   * @private
   */
  private _end() {
    // game is not longer running dude
    this._isRunning = false

    this._hud!.renderGameOver()
  }

  /**
   *
   * @private
   */
  private _detectCollisions() {
    if (this._player!.collides(this._beacon!)) {
      this._player!.speak("It's locked!")
    }

    if (
      this._crosshair &&
      this._crosshair!.collides(this._beacon!)
    ) {
      this._beacon = new Beacon(
        this._resources,
        this._height,
        this._width
      )

      this._stage.removeChild(this._map!)
      this._map = new Loading(
        this._resources,
        this._width,
        () => {
          this._map = new MaensionOutside(
            this._resources,
            this._width
          )
        }
      )
      this._stage.addChild(this._map!)
    }

    for (const enemy of this._enemies) {
      if (
        ActorStates.died !== enemy.state &&
        ActorStates.died !== this._player!.state
      ) {
        // does the player collide the enemy?
        if (this._player!.collides(enemy)) {
          const playerHit = this._player!.hitBy(enemy)

          if (playerHit.objectTookDamage) {
            this._drawBlood(this._player!)
            this._updateHud()
          }

          // we died and now lives left :(
          if (playerHit.died) {
            // end the game
            this._end()
          }
        }
      }
    }
  }

  /**
   *
   * @param actor: the bleeding actor
   * @private
   */
  private _drawBlood(actor: ActorBase) {
    for (let i = 0; i < 5; i++) {
      const blood = new Blood(this._resources)

      blood.x = actor.x + MathHelper.getRandomInt(-20, 20)
      blood.y =
        actor.y +
        actor.height +
        MathHelper.getRandomInt(-20, 20)
      blood.rotation = MathHelper.getRandomInt(0, 10)

      this._map?.backgroundContainer.addChild(blood)
    }
  }

  /**
   *
   * @private
   */
  private _updateHud() {
    this._hud!.update()
  }

  /**
   *
   * @private
   */
  private _updateKI() {
    for (const enemy of this._enemies) {
      if (ActorStates.died !== enemy.state) {
        enemy.moveTowards(this._player!.position)
      }
    }
  }

  /**
   *
   * @private
   */
  private _moveActors() {
    const { _player } = this
    let playerMoved = _player!.move(
      this._height,
      this._map!.width
    )

    if (playerMoved) {
      // yep, so try to scroll the map
      this._map?.shallScroll(_player!.realx, _player!.vx)
      this._updateCrosshair()
    }

    // move the enemies
    for (const enemy of this._enemies) {
      enemy.move(this._height, this._map!.width)
    }
  }

  private _outlineMouse() {
    if (
      this._mouseLine &&
      this._mouseLocation &&
      this._player &&
      this._map
    ) {
      this._mouseLine.clear()
      this._mouseLine.lineStyle(1, Config.constants.white)

      // from
      this._mouseLine.position = this._player.weaponLocation

      // to
      const { x, y } = this._mouseLine.toLocal(
        this._mouseLocation
      )
      this._mouseLine.lineTo(x, y)

      this._mouseLine.endFill()
    }
  }

  private _switchWeapon() {
    if (this._isRunning) {
      this._player!.switchWeapon()
      this._updateHud()
    }
  }

  private _reload() {
    if (this._isRunning) {
      this._player!.reload()
      this._updateHud()
    }
  }

  private _updateMouseLocation(mouseLocation: Point) {
    if (this._isRunning) {
      this._mouseLocation = mouseLocation
      this._updateCrosshair()
    }
  }

  /**
   *
   * @param elapsed
   * @param delta
   * @public
   */
  public tick(elapsed: number, delta: number) {
    super.tick(elapsed, delta)

    Gamepads.updateStatus()

    this._hud!.tick(delta)

    if (this._isRunning) {
      this._player!.tick(delta)
      this._map!.tick(delta)

      const elapsedSeconds = parseInt(
        (elapsed / 1000).toString()
      )

      // game has not started
      if (!this._startOffset) {
        this._startOffset = elapsedSeconds
      }

      const correctedElapsedSeconds =
        elapsedSeconds - this._startOffset

      if (
        correctedElapsedSeconds !== this._elapsedSeconds
      ) {
        this._elapsedSeconds = correctedElapsedSeconds
        this._updateHud()
      }

      this._detectCollisions()
      this._updateKI()
      this._moveActors()

      // sort by y
      const sort = (a: DisplayObject, b: DisplayObject) => {
        const ab = a.getBounds()
        const bb = b.getBounds()

        if (a.y + ab.height < b.y + bb.height - 5) {
          return -1
        }

        if (a.y + ab.height > b.y + bb.height - 5) {
          return 1
        }

        return 0
      }

      // ensure z index for actors
      this._map?.actorContainer.children.sort(sort)

      if (Config.outlineObjects) {
        // outline player
        this._player!.outline()

        // outline enemies
        for (const enemy of this._enemies) {
          enemy.outline()
        }

        // outline mouse line
        this._outlineMouse()
      }
    }
  }
}
