import {
  Text,
  Container,
  Sprite,
  TextStyle,
  IResourceDictionary
} from "pixi.js"
import { Player } from "../actors"
import { AnimationBase } from "../../engine/objects/animation-base";

export class HUD extends AnimationBase {
  private _player: Player
  private _health: Text

  // round icons
  private _rounds: Container
  // weapon icon
  private _weapon?: Sprite

  // change to "you are dead ..."
  private _gameOverSign?: Text

  private _width: number
  private _height: number

  static bigText: TextStyle = {
    fontFamily: "Arial Black",
    fontSize: "48px",
    fontWeight: "bold",
    fill: "#fff",
    dropShadow: true,
    dropShadowColor: "#000",
    align: "center"
  } as TextStyle

  constructor(
    resources: IResourceDictionary,
    player: Player,
    width: number,
    height: number
  ) {
    super(resources)

    const which = "things.hud"

    const res = resources[which]

    this._width = width
    this._height = height

    console.log(
      "Setting up HUD",
      "width:",
      this._width,
      "height:",
      this._height
    )

    this._player = player

    const smallTextConfig = {
      ...HUD.bigText,
      fontSize: "12px"
    }

    const playerName = new Text(player.playerName, {
      ...smallTextConfig,
      fill: "#ffff30"
    })
    playerName.x = this._width / 3 - playerName.width / 2
    playerName.y = this._height - 47
    this.addChild(playerName)

    this._health = new Text("", smallTextConfig)

    this._health.x = playerName.x
    this._health.y = this._height - 23
    this.addChild(this._health)

    this._rounds = new Container()
    this._rounds.x = this._width / 3 + 77
    this._rounds.y = this._height - 33

    this.addChild(this._rounds)

    const healthBorder = new Sprite(
      res.textures!["side.png"]
    )

    healthBorder.x =
      this._width / 2 -
      (healthBorder.width + healthBorder.width / 2) +
      2
    healthBorder.y = this._height - healthBorder.height - 2

    this.addChild(healthBorder)

    const roundsBorder = new Sprite(
      res.textures!["center.png"]
    )
    roundsBorder.x =
      this._width / 2 - roundsBorder.width / 2 + 1
    roundsBorder.y = this._height - roundsBorder.height - 2

    this.addChild(roundsBorder)

    const weaponBorder = new Sprite(
      res.textures!["side.png"]
    )

    weaponBorder.x =
      this._width / 2 +
      (roundsBorder.width + weaponBorder.width / 2) -
      2
    weaponBorder.y = this._height - weaponBorder.height - 2

    weaponBorder.scale.x = -1

    this.addChild(weaponBorder)

    this.update()
  }

  private _renderWeapon() {
    this.removeChild(this._weapon!)

    this._weapon = new Sprite(
      this._player.weapon.sprite!.texture
    )
    this._weapon.scale.x =
      this._player.weapon.sprite!.scale.x * 2
    this._weapon.scale.y =
      this._player.weapon.sprite!.scale.y * 2

    this._weapon.x =
      this._width / 2 + (this._weapon.width + 27)
    this._weapon.y = this._height - 40

    this.addChild(this._weapon)
  }

  private _renderRounds() {
    this._rounds.removeChildren()

    for (let x = 0; x < this._player.weapon.rounds; x++) {
      const roundSprite = new Sprite(
        this._player.weapon.roundsSprite?.texture
      )
      roundSprite.scale.x = 0.1
      roundSprite.scale.y = 0.1
      roundSprite.x = 10 * x
      roundSprite.y = 10

      this._rounds.addChild(roundSprite)
    }
  }

  public update() {
    this._health.text = this._player.health + " HP"

    this._renderRounds()
    this._renderWeapon()
  }

  public renderGameOver() {
    this._gameOverSign = new Text(
      "Game Over\nPress Enter to retry!",
      {
        ...HUD.bigText,
        fill: "#f00"
      }
    )

    this.addChild(this._gameOverSign)

    // place game over sign in center of screen
    this._gameOverSign.x =
      this._width / 2 - this._gameOverSign.width / 2
    this._gameOverSign.y =
      this._height / 2 - this._gameOverSign.height / 2
  }

  public tick(delta: number) {
    if (this._gameOverSign) {
      this._gameOverSign.alpha += delta / 1000
      if (this._gameOverSign.alpha > 1) {
        this._gameOverSign.alpha = 0.2
      }
    }
  }
}
